import axios from 'axios';

export default {
	searchTitle: (query) => {
		return axios.get(`api/books/search/${query}`);
	},
	loadSaved: (query) => {
		return axios.get('/api/books');
	},
	deleteSaved: (query) => {
		return axios.delete(`/api/books/${query}`);
	},
	saveBook: (query) => {
		return axios.post('/api/books', query);
	}
}