import React from "react";
import { useState, useEffect } from "react";
import { Container, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import API from "../api/API";

function Home() {
	const initial = [];
	const [books, setBooks] = useState(initial);
	const [saveBook, setSavedBook] = useState({});

	useEffect(() => {
		API.searchTitle('The Martian').then(res => {
			setBooks(res.data.items);
		}).catch(err => {
			showAlert(err, 'error');
			console.log(err);
		});
	}, []);

	function handleInputChange(e) {
		const { name, value } = e.target;
	}

	function saveBookItem(book) {
		let newBook = {
			title: book.title,
			author: book.authors[0],
			description: book.description,
			thumbnail: book.imageLinks.smallThumbnail,
			link: book.infoLink
		}

		.saveBook(newBook).then(res => {
			setSavedBook(res.data);
			showAlert(res.data, 'success');
		}).catch(err => {
			console.log(err);
			showAlert(err, 'error');
		});
	}

	function showAlert(data, type) {

	}

	return (
		<Container>
			<br />
			<div className="text-center">
				<h1>Results</h1>
				{books.length ? (
					<ul>
						{books.map((item) => (
							<li key={item.id}>
								<img
									src={item.volumeInfo.imageLinks.smallThumbnail}
									alt={item.volumeInfo.title}
								/>
								<Link to={`/books/${item.id}`} />
								<h2>{item.volumeInfo.title}</h2>
								<h3>By: {item.volumeInfo.authors[0]}</h3>
								<p>{item.volumeInfo.description}</p>
								<Button onClick={() => saveBook(item.volumeInfo)}>Save</Button>
							</li>
						))}
					</ul>
				) : (
					<h1>No results!</h1>
				)}
			</div>
		</Container>
	);
}

export default Home;
